// Webfonts

// see http://compass-style.org/reference/compass/css3/font_face/
// @include font-face($name, $font-files, $eot, $weight, $style);

// Headings

h1 {
	font: $fontstyle-default $fontweight-secondary #{$fontsize-biggest}/#{$lineheight-default} $fontfamily-secondary;
}
h2 {
	font: $fontstyle-default $fontweight-secondary #{$fontsize-bigger}/#{$lineheight-default} $fontfamily-secondary;
}
h3 {
	font: $fontstyle-default $fontweight-secondary #{$fontsize-big}/#{$lineheight-default} $fontfamily-secondary;
}
h4 {
	font: $fontstyle-default $fontweight-default-bold #{$fontsize-default}/#{$lineheight-default} $fontfamily-default;
}

// Text

a {
	color: $color-link;

	&:link {}
	&:visited {}
	&:focus, &:hover {
		color: $color-link-hover;
	}
	&:active {}
	&.current-active {} // class for links which point to current page
}
strong {
	font-weight: $fontweight-default-bold;
}
p {
	a {}
	strong {}
	em {}
	mark {}
	code {}
	small {}
	del {}
	ins {}
	sub {}
	sup {}
}
blockquote {
	p {}
	cite {}
}

// Lists

ul {
	li {}
}
ol {
	li {}
}
dl {
	dt {
		font-weight: $fontweight-default-bold;
	}
	dd {}
}


// Forms

form {
	fieldset {
		legend {}
	}
	.form-field {
		&.required {
			label {
				&:after {
					content: "*";
				}
			}
			input {}
			textarea {}
		}
		&.valid {
			label {}
			input {}
			textarea {}
		}
		&.invalid {
			label {}
			input {}
			textarea {}
			select {}
		}
	}
	label {
		display: inline-block;
		width: 20%;
	}
	small {}
	input, textarea, select {
		&:required {}
		&:valid {}
		&:invalid {}
	}
	input {
		&[type=text], &[type=password], &[type=search], &[type=date], &[type=email], &[type=number], &[type=url], &[type=time], &[type=tel] {}
		&[type=hidden] {}
		&[type=file] {}
		&[type=submit] {}
		&[type=checkbox], &[type=radio] {}
	}
	textarea {}
	select {
		optgroup {}
	}
	button {}
}

// Tables

table {
	table-layout: fixed;
	width:100%;

	thead {}
	tfoot {}
	tbody {}
	tr {
	}
	th {
		font-weight: $fontweight-default-bold;
	}
	td {}
	th, td {
		&.no-break {
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		&.number {
			font-family: $fontfamily-monospace;
			text-align:right;
		}
	}
}

// Images

.figure {
	.figcaption {}
}

// UI Elements

.browsehappy {
	display:none;
}

@if ($switch-develop) {
	div,table {
		outline:1px solid rgba(255,0,0,0.5);
	}
	h1,h2,h3,h4 {
		outline:1px solid rgba(0,255,0,0.5);
	}
	td,th {
		outline:1px solid rgba(0,0,255,0.5);
	}

	@media #{$media-responsive-mobile} { .debug {/*media-responsive-mobile*/ }}
	@media #{$media-responsive-tablet} { .debug {/*media-responsive-tablet*/ }}
	@media #{$media-responsive-tablet-landscape} { .debug {/*media-responsive-tablet-landscape*/ }}
	@media #{$media-responsive-desktop} { .debug {/*media-responsive-desktop*/ }}
	@media #{$media-responsive-mobile--smaller} { .debug {/*media-responsive-mobile--smaller*/ }}
	@media #{$media-responsive-tablet--smaller} { .debug {/*media-responsive-tablet--smaller*/ }}
	@media #{$media-responsive-tablet-landscape--smaller} { .debug {/*media-responsive-tablet-landscape--smaller*/ }}
	@media #{$media-responsive-desktop--smaller} { .debug {/*media-responsive-desktop--smaller*/ }}
	@media #{$media-responsive-mobile--bigger} { .debug {/*media-responsive-mobile--bigger*/ }}
	@media #{$media-responsive-tablet--bigger} { .debug {/*media-responsive-tablet--bigger*/ }}
	@media #{$media-responsive-tablet-landscape--bigger} { .debug {/*media-responsive-tablet-landscape--bigger*/ }}
	@media #{$media-responsive-desktop--bigger} { .debug {/*media-responsive-desktop--bigger*/ }}
	@media tty { .debug {/* Old-IE*/ }}
}
